.error {
    text-align: center;
    height: 30px;
    color:red
}

.wrapper {
    font-family: sans-serif;
    position: relative;
    display: flex;
}

.input_div {
    display: flex;
}

.widget {
    width: 620px;
    margin: 0px auto;
    background-color: white;
    border: 1px solid;
}

.info {
    display:flex;
}

.input_div {
    border-bottom: 1px solid;
    margin:0;
}

#search {
    width: 100%;
    height: 25px;
    border: none;
    padding:10px;
}

.input_div button {
    display: inline-block;
    border: none;
    background:none;
    font-size: 24px;
    cursor: pointer;
    align-items: start;
    transition: all 0.3s linear;
}

.input_div input:focus {
    outline: none;
}

.weather_information, .weather_favorite {
    width:100%;
}

.tabs_blocks {
    height:100%;
    display: flex;
    flex-direction:column;
    justify-content:space-between;
    padding:0;
}

.tabs_body {
    height: 200px;
    padding:10px;
    border-right:1px solid black;
}

.tabs_block {
    display: none;
}

.tabs_block.active {
    display: block;
    height:100%;
}

.tabs_list {
    display: flex;
    justify-content:space-between;
}

.tabs_item {
    width:100%;
    border: 1px solid black;
    display: flex;
    justify-content:center;
    align-items:center;
    background: white;
    cursor:pointer;
    border-left:none;
    border-bottom:none;
}
.tabs_item.active {
    background: #000;
    color:white;
    padding:12px
}

.favorite_block h4 {
    padding:10px;
    margin: 0;
    border-bottom:1px solid black;
}

.favorite_block ul {
    list-style-type:none;
    margin:0;
    height: 200px;
    overflow-y:scroll;
    padding: 10px;
}

.favorite_block ul::-webkit-scrollbar {
    width: 10px;
}

.favorite_block ul::-webkit-scrollbar-track {
    border-radius: 50px;
}

.favorite_block ul::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: linear-gradient(180deg, #ddd, #999);
} 
.favorite_block li {
    padding:5px;
    margin-bottom:5px;
    display: flex;
    justify-content:space-between;
    border-radius:5px;
    background: #efefef;
}  

.favorite_block li span {
    width:100%;
}

.favorite_block li button {
    border:none;
    background: transparent;
    transform:scale(2);
}

.favorite_block li button:hover{
    color: red;
}

.tab_now {
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
}

.tab_now  .temperature {
    font-size: 40px;
    font-weight: bold;
}

.tab_now img {
    display: block;
    margin: auto;
}

.tab_details p {
    margin: 10px;
}

.cityFavorite {
    display: flex;
    justify-content:space-between;
    align-items:center;
}

.cityFavorite button {
    display: inline-block;
    border: none;
    background:none;
    font-size: 24px;
    cursor: pointer;
    align-items: start;
    transition: all 0.3s linear;
}